
export default ({imgSrc, imgStyle, name, comment}) => (
    <div className='w-full border-r-3 bg-primary flex p-3'>
        <div className="flex flex-col ">
            <img src={imgSrc} style={imgStyle} className="w-15 h-15 border border-gray-700" />
            <img src={imgSrc} style={imgStyle} className="w-15 h-15 border border-gray-700" />
        </div>
        <div className="flex flex-col ml-3">
            <h4 className="font-semibold text-white text-xl mb-3">{name}</h4>
            <p className="text-base text-white font-light">{comment}</p>
        </div>
    </div>
)